import React, { Component } from 'react';
import MainMenu from './MainMenu';
import { getPages } from './GetData';
import HomePageContent from './HomePageContent';
import HomeBanner from './HomeBanner';
import HeaderLogo from './HeaderLogo';
import logo from './../images/logo.svg';

class HomeTemplate extends Component {

    state = {
        pages: [],
        hoverTarget: '',
    };

    handleHoverEnter = (event) => {
        //console.log(event.target);
        //console.log(event.target.id);
        //console.log(this.state.hoverEvent.hover);
        this.setState({ 
            hoverTarget: event.target.id
        });
    };

    handleHoverLeave = (event) => {
        this.setState({ 
            hoverTarget: ''
        });
    };

    updateArr = () => {
        this.setState({
            newArr : this.state.pageContent
        })
    }


    // initContent = () => {
    //     this.state.pages.map((page, index) => {
    //         //console.log(page);
    //         if ( page.slug === 'home') {
    //             this.state.pageContent.push({
    //                 id : page.id, 
    //                 slug : page.slug, 
    //                 title : page.title.rendered,
    //                 content : page.content.rendered,
    //                 banner: page.acf.homeBanner
    //             });
    //         }
    //         return false;
    //     })
    //     this.state.pageContent.map((detail, index) => {
    //         //console.log(detail.banner);
    //         if (detail.banner !== undefined) {
    //             detail.banner.map((banner, key) => {
    //                 //console.log(banner);
    //                 this.state.bannerContent.push({
    //                     bg_image : banner.bg_image,
    //                     sidebar_content : banner.sidebar_content,
    //                     sidebar_title : banner.sidebar_title,
    //                     slug : banner.slug,
    //                     title : banner.title
    //                 });
    //                 return false;
    //             });
    //         }

    //         return false;
    //     })
    //     //console.log(this.state.bannerContent);
    // };

    // get filtered content
    componentDidMount() {
        let postUrl = `${getPages}`;
        fetch(postUrl)
        .then(results => results.json())
        .then(results => {
            this.setState({
               pages: results
            })
        })
        
    }

    render() {

        //this.initContent();

        return (
            <div className="app__container container-fluid">
                <div className="main-content row home">
                    <div className="app__sidebar col-md-4">
                        <HeaderLogo src={logo} alt="Innermost Digital Logo new"/>
                        <div className="sb-content">
                            <HomePageContent 
                                pageContent={this.state.pages} 
                                hoverTarget={this.state.hoverTarget}
                            />
                        </div>
                    </div>
                    <div className="app__home-banner col-md-8">
                        <MainMenu />
                        <HomeBanner 
                            bannerContent={this.state.pages} 
                            handleHoverEnter={this.handleHoverEnter}
                            handleHoverLeave={this.handleHoverLeave}
                        />
                    </div>
                </div>
            </div>
        );

    }
}
export default HomeTemplate;


