
// Import components
import React, {Component} from 'react';
import HeaderLogo from './HeaderLogo';
import logo from './../images/logo.svg';

class Sidebar extends Component {

	render() {
		let sbContent = {
			heading : '',
			impactHeading : '',
			impactStats : []
		}

		if (this.props.content.template === "page-templates/page-flexible-content.php" || this.props.content.template === "page-templates/page-casestudy.php") {
			//console.log(this.props.content.acf);
			if ( 
				this.props.content.acf.sidebar_flexible_content !== null 
				&& this.props.content.acf.sidebar_flexible_content !== false ) {

				this.props.content.acf.sidebar_flexible_content.map((content, key) => {
					if (content.acf_fc_layout === "heading") {
						sbContent.heading = content.text;
					}
					if (content.acf_fc_layout === "impact_heading") {
						sbContent.impactHeading = content.text;
					}
					if (content.acf_fc_layout === "impact_stats") {
						sbContent.impactStats = content.stats;
					}
                    return true;
                })
			}
		}
		
		return (
			<React.Fragment>
				<HeaderLogo src={logo} alt="Innermost Digital Logo new"/>
				<div className="sb-content sb-impact">
					<h3 className="sb__head">{sbContent.heading}</h3>
					<h4 className="sb__subhead">{sbContent.impactHeading}</h4>
					{
						sbContent.impactStats.map((content, key) => {
							return (
								<div key={key} className="sb__stats">
									<span className="stats__num">{content.number}</span>
									<span className="stats__head">{content.heading}</span>
								</div>
							)
						})
					}
				</div>
			</React.Fragment>
		)
	}
	
}
export default Sidebar;


