
// Import components
import React, {Component} from 'react';


class FlexContent extends Component {

	render() {
		
		const flexArray = this.props.flexContent;
		return (
			flexArray.map((content, key) => {
				console.log(content)

				// flex heading module
				if (content.acf_fc_layout === "heading") {
					if (content.type === "h2") {
						return (
							<div key={key} className={"block flex-"+content.acf_fc_layout+"__"+key+" col-xs-12 "+content.class+" "+content.align}>
								<h2 className={'heading '}>{content.text}</h2>
							</div>
						)
					}
					if (content.type === "h3") {
						return (
							<div key={key} className={"block flex-"+content.acf_fc_layout+"__"+key+" col-xs-12 "+content.class+" "+content.align}>
								<h3 className={'heading ' + content.align}>{content.text}</h3>
							</div>
						)
					}
					if (content.type === "h4") {
						return (
							<div key={key} className={"block flex-"+content.acf_fc_layout+"__"+key+" col-xs-12 "+content.class+" "+content.align}>
								<h4 className={'heading ' + content.align}>{content.text}</h4>
							</div>
						)
					}
					return (
						<div key={key} className={"block flex-"+content.acf_fc_layout+"__"+key+" col-xs-12 "+content.class+" "+content.align}>
							<h1 className={'heading ' + content.align}>{content.text}</h1>
						</div>
					)
				}

				// flex text module
				if (content.acf_fc_layout === "text" ) {
					
					// get row bg
					let flexTextBg;
					if (content.columns.row_background_image) {
						flexTextBg = {
							backgroundImage: 'url(' + content.columns.row_background_image + ')',
						};
					}
					
					if (content.columns.columns === "2") {
						return (
							<div key={key} className={"block flex-"+content.acf_fc_layout+"__"+key+" col-xs-12 "+content.columns.class} style={flexTextBg}>
								<div className="row">
									<div
										className={"col-1 col-xs-12 col-sm-"+content.column_1.col_width+" col-sm-offset-"+content.column_1.col_offset+" "+content.column_1.align}
										dangerouslySetInnerHTML={{__html: content.column_1.textarea}}
									/>
									<div
										className={"col-2 col-xs-12 col-sm-"+content.column_2.col_width+" col-sm-offset-"+content.column_2.col_offset+" "+content.column_2.align}
										dangerouslySetInnerHTML={{__html: content.column_2.textarea}}
									/>
								</div>
							</div>
						)	
					}
					if (content.columns.columns === "3") {
						return (
							<div key={key} className={"block flex-"+content.acf_fc_layout+"__"+key+" col-xs-12 "+content.columns.class} style={flexTextBg}>
								<div className="row">
									<div
										className={"col-1 col-xs-12 col-sm-"+content.column_1.col_width+" col-sm-offset-"+content.column_1.col_offset+" "+content.column_1.align}
										dangerouslySetInnerHTML={{__html: content.column_1.textarea}}
									/>
									<div
										className={"col-2 col-xs-12 col-sm-"+content.column_2.col_width+" col-sm-offset-"+content.column_2.col_offset+" "+content.column_2.align}
										dangerouslySetInnerHTML={{__html: content.column_2.textarea}}
									/>
									<div
										className={"col-3 col-xs-12 col-sm-"+content.column_3.col_width+" col-sm-offset-"+content.column_3.col_offset+" "+content.column_3.align}
										dangerouslySetInnerHTML={{__html: content.column_3.textarea}}
									/>
								</div>
							</div>
						)	
					}
					return (
						<div key={key} className={"block flex-"+content.acf_fc_layout+"__"+key+" col-xs-12 "+content.columns.class} style={flexTextBg}>
							<div className="row">
								<div
									className={"col-1 col-xs-12 col-sm-"+content.column_1.col_width+" col-sm-offset-"+content.column_1.col_offset+" "+content.column_1.align}
									dangerouslySetInnerHTML={{__html: content.column_1.textarea}}
								/>
							</div>
						</div>
					)
				}

				// flex callout module
				if (content.acf_fc_layout === "callout") {
					const haveStyle = () => {
						if (content.style !== "none") {
							return true;
						}
					}
					// if have content, load it
					const haveContent = () => {
						if (content.textarea) {
							return (
								<div className="callout-content" dangerouslySetInnerHTML={{__html: content.textarea}} />
							)
						}
					}
					// callout with button
					if (content.btn_style === "button" || content.btn_style === "outline") {
						return (
							<div key={key} 
								className={"block flex-"+content.acf_fc_layout+"__"+key+" col-xs-12 "+content.style+" "+content.class} 
								style={haveStyle() ? {  backgroundColor: content.bg_colour,backgroundImage: `url(${content.bg_img})` } : { background: "none" } } >
								<h3 className="callout-heading" dangerouslySetInnerHTML={{__html: content.heading}}></h3>
								{haveContent()}
								<a href={content.link.slug} className={"basic-btn "+content.btn_style} title={content.link.title}>{content.link.title}</a>
							</div>
						)
					}
					// callout with link on wrapper
					if (content.btn_style === "link") {
						return (
							<a key={key} 
								href={content.link.slug}
								className={"block flex-"+content.acf_fc_layout+"__"+key+" col-xs-12 "+content.style+" "+content.class} 
								style={haveStyle() ? {  backgroundColor: content.bg_colour,backgroundImage: `url(${content.bg_img})` } : { background: "none" } } >
								<h3 className="callout-heading" dangerouslySetInnerHTML={{__html: content.heading}}></h3>
								{haveContent()}
								<div className={"basic-btn "+content.btn_style} title={content.link.title}>{content.link.title}</div>
							</a>
						)
					}
					// basic text callout
					return (
						<div key={key} 
							className={"block flex-"+content.acf_fc_layout+"__"+key+" col-xs-12 "+content.class} 
							style={haveStyle() ? {  backgroundColor: content.bg_colour,backgroundImage: `url(${content.bg_img})` } : { background: "none" } } >
							<h3 className="callout-heading" dangerouslySetInnerHTML={{__html: content.heading}}></h3>
							{haveContent()}
						</div>
					)
				}

				// flex image module
				if (content.acf_fc_layout === "image") {
					const haveAlt = (alt) => {
						if (alt === undefined || alt === "") {
							return false;
						}
						return true;
					}
					const numOfChilds = 12 / content.image_row.length;
					if (content.image === false && content.image_row === false) {
						return false;
					}
					return (
						<div key={key} 
							className={"block flex-"+content.acf_fc_layout+"__"+key+" col-xs-12 "+content.class} 
							 >
							<div className="row">
							 	{ content.image_row.map((content, key) => {
							 		if (content.link) {
							 			return (
							 				<a key={key} href={content.link} title={content.link} className={"child_img_"+ key + " col-xs-12 col-sm-" +  numOfChilds } target={content.target}>
							 					<img src={content.image.url} alt={haveAlt(content.image.alt) ? content.image.alt : content.image.title } />
							 				</a>
							 			)
							 		}
							 		return (
							 			<div key={key} className={"child_img_"+ key + " col-xs-12 col-sm-" +  numOfChilds }>
							 				<img src={content.image.url} alt={haveAlt() ? content.image.alt : content.image.title } />
							 			</div>
							 		)
							 	}) }
							</div>
						</div>
					)
				}
				return false;
			})
		)
		//return false;
	}
	
}
export default FlexContent;


