
// Import components
import React, {Component} from 'react';


class HomePageContent extends Component {

	render() {
		let bannerContent = {}

		this.props.pageContent.map((content, key) => {
			if ( content.slug === 'home') {
				//console.log(content.acf.homeBanner)
				content.acf.homeBanner.map((content, key) => {
					if ( content.slug === this.props.hoverTarget) {
						bannerContent = content;
					}
					return false;
				})
			}
			return false;
		})


		if (this.props.hoverTarget.length) {
			return (
				<React.Fragment>
					<div className={'banner-content ' + bannerContent.slug} >
						<h2 dangerouslySetInnerHTML={{__html: bannerContent.sidebar_title}}></h2>
						<p dangerouslySetInnerHTML={{__html: bannerContent.sidebar_content}}></p>
					</div>
				</React.Fragment>
			)
		}
		return (
			<React.Fragment>
				{
					this.props.pageContent.map((content, key) => {
						if ( content.slug === 'home') {
							return (
								<div key={key} className="page-content" dangerouslySetInnerHTML={{__html: content.content.rendered}}/>
							)
						}
						return false;
					})
				}
			</React.Fragment>
		)
	}
	
}
export default HomePageContent;


