
// Import components
import React, {Component} from 'react';
import { Link } from 'react-router-dom';


class HomeBanner extends Component {

	render() {
        let bannerContent = []

        this.props.bannerContent.map((content, key) => {
            if ( content.slug === 'home') {
                //console.log(content.acf.homeBanner)
                content.acf.homeBanner.map((banner, key) => {
                    bannerContent.push({
                        bg_image : banner.bg_image,
                        sidebar_content : banner.sidebar_content,
                        sidebar_title : banner.sidebar_title,
                        slug : banner.slug,
                        title : banner.title
                    });
                    return true;
                })
            }
            return false;
        })

        return (
            <React.Fragment>
                {
                    bannerContent.map((bannerSlideContent, key) => {
                        return (
                            <Link 
                                to={'/'+bannerSlideContent.slug+'/'} 
                                className='slide'
                                id={bannerSlideContent.slug}
                                key={key}
                                title={bannerSlideContent.title} 
                                onMouseEnter={this.props.handleHoverEnter}
                                onMouseLeave={this.props.handleHoverLeave}
                                >
                                    <div className="slide-content" id={bannerSlideContent.slug}>
                                        <h2>{bannerSlideContent.title}</h2>
                                    </div>
                                    <img src={bannerSlideContent.bg_image.url} alt={bannerSlideContent.bg_image.alt} />
                            </Link>
                        )
                    })
                }
            </React.Fragment>
        )

    }
	
}
export default HomeBanner;


