
// Check WP Host
const checkWpHost = () => {
	if (window.location.hostname === 'localhost') {
		console.log('host: ' + window.location.hostname);
		return (
			"http://radii-multisite-sme.local/innermost/wp-json/wp/v2/"
		)
	}
	if (window.location.hostname === 'goradii.com' || window.location.hostname === 'adrianluo.github.io') {
		console.log('host: ' + window.location.hostname);
		return (
			"http://radii-multisite-sme.goradii.com/innermost/wp-json/wp/v2/"
		)
	}
	if (window.location.hostname === 'innermost.com') {
		console.log('host: ' + window.location.hostname);
		return (
			"http://innermost.com/innermost/wp-json/wp/v2/"
		)
	}
}

// Define wp api path
export const appUrl = checkWpHost();

// get posts
export const getPosts = `${appUrl}posts`;

// get pages
export const getPages = `${appUrl}pages`;

