
// Import components
import React, {Component} from 'react';
import MainMenu from './MainMenu';
import FlexContent from './FlexContent';


class CaseStudyContent extends Component {

	render() {
		
		const csContent = this.props.content.acf;

		// load intro image
		const haveIntroImg = () => {
			if (csContent.case_study_fg_img) {
				return (
					<img src={csContent.case_study_fg_img} className="col-xs-12 col-sm-9 fg-img" alt="Intro Graphic" />
				)
			}
		}

		// if wp content & flex content exist, return both
		if (this.props.content.length !== 0 && csContent.flexible_content !== null 
						&& csContent.flexible_content !== false) {
			const csIntroStyle = {
				backgroundImage: 'url(' + csContent.case_study_bg_img + ')',
			};
			return (
				<React.Fragment>
					<div className="flex-content row">
						<MainMenu />
						<div className="block intro col-xs-12" style={csIntroStyle}>
							<div className="row">
								{haveIntroImg()}
								<div className="col-xs-12 col-sm-4 intro-txt" dangerouslySetInnerHTML={{__html: this.props.content.content.rendered}} />
							</div>
						</div>
						<FlexContent flexContent={csContent.flexible_content} />
					</div>
				</React.Fragment>
			)
		}
		//if wp content exist, return wp content
		if (this.props.content.length !== 0) {
			return (
				<React.Fragment>
					<div className="page-content row">
						<MainMenu />
						<div className="col-xs-12" dangerouslySetInnerHTML={{__html: this.props.content.content.rendered}} />
					</div>
				</React.Fragment>
			)
		}
		return false;
	}
	
}
export default CaseStudyContent;


