import React, { Component } from 'react';
import { getPages } from './GetData';
import Sidebar from './Sidebar';
import PageContent from './PageContent';

class TeamTemplate extends Component {
	state = {
        error: null,
        page: [],
    };

    componentDidMount() {
        let postUrl = `${getPages}`;
        fetch(postUrl)
        .then(results => results.json())
        .then(
            (results) => {
                results.map((result, key) => {
                    if (result.status === "publish" && result.slug === "our-team") {
                        return (
                            this.setState({
                                page: result
                            })
                        )
                    }
                    return false;
                })
            },
            (error) => {
                this.setState({
                    error: true
                });
            }
        )
    }

    render() {
        return (
            <div className="app__container container-fluid">
                <div className="main-content team row">
                    <div className="app__sidebar col-md-3">
                        <Sidebar content={this.state.page} />
                    </div>
                    <div className="app__main col-md-9">
                        <PageContent content={this.state.page} />
                    </div>
                </div>
            </div>
        );

    }
}

export default TeamTemplate;


