
// Import components
import React, {Component} from 'react';
import MainMenu from './MainMenu';
import FlexContent from './FlexContent';


class PageContent extends Component {

	render() {
		
		const pageContent = this.props.content.acf;

		// load intro image
		const haveIntroImg = () => {
			if (pageContent.page_intro_fg_img) {
				return (
					<img src={pageContent.page_intro_fg_img} className="col-xs-12 fg-img" alt="Intro Graphic" />
				)
			}
		}

		// if wp content & flex content exist, return both
		if (this.props.content.length !== 0 && pageContent.flexible_content !== null && pageContent.flexible_content !== false) {
			const introBg = {
				backgroundImage: 'url(' + pageContent.page_intro_bg_img + ')',
			};
			return (
				<React.Fragment>
					<div className="flex-content row">
						<MainMenu />
						<div className="block intro col-xs-12" style={introBg}>
							<div className="row">
								<div className="col-xs-12 intro-txt" dangerouslySetInnerHTML={{__html: this.props.content.content.rendered}} />
								{haveIntroImg()}
							</div>
						</div>
						<FlexContent flexContent={pageContent.flexible_content} />
					</div>
				</React.Fragment>
			)
		}
		//if wp content exist, return wp content
		if (this.props.content.length !== 0) {
			return (
				<React.Fragment>
					<div className="page-content row">
						<MainMenu />
						<div className="col-xs-12" dangerouslySetInnerHTML={{__html: this.props.content.content.rendered}} />
					</div>
				</React.Fragment>
			)
		}
		return false;
	}
	
}
export default PageContent;


